<template>
<div class="container">
  <v-modal>
    <div class="login">
      <div class="login__social">
        <h1 class="login__social__title">Social media {{type}}</h1>
        <v-button outline>
          <facebook-icon/>
          Facebook
        </v-button>
        <v-button outline>
          <google-icon/>
          Google
        </v-button>
      </div>

      <div class="login__separator"></div>

      <div class="login__form">
        <h1 class="login__form__title">{{type}}</h1>

        <form @submit="validateForm">
          <v-text-field
            v-if="type === 'signup'"
            v-model="name"
            name="name"
            label="Name"
            :error="errors.name"
          ></v-text-field>

          <v-text-field
            v-model="email"
            name="email"
            label="Email"
            :error="errors.email"
          ></v-text-field>

          <v-text-field
            v-model="password"
            name="password"
            type="password"
            label="Password"
            :error="errors.password || errors.form"
          ></v-text-field>

          <div v-if="type === 'login'" class="login__form__signup">
            <router-link to="/signup">
              Don't have an account?
            </router-link>
          </div>

          <v-button type="submit">{{type}}</v-button>
        </form>
      </div>
    </div>
  </v-modal>
</div>
</template>

<script>
import FacebookIcon from '@/components/common/icons/Facebook.vue';
import GoogleIcon from '@/components/common/icons/Google.vue';
import VTextField from '@/components/common/VTextField.vue';
import VModal from '@/components/common/VModal.vue';
import VButton from '@/components/common/VButton.vue';
import { login, signup } from '@/services/api';
import { mapActions } from 'vuex';

export default {
  name: 'Login',
  components: {
    FacebookIcon,
    GoogleIcon,
    VTextField,
    VModal,
    VButton,
  },
  props: {
    type: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      errors: {
        name: '',
        email: '',
        password: '',
        form: '',
      },
      name: '',
      email: '',
      password: '',
      longitude: 0,
      latitude: 0,
    };
  },
  computed: {
    isSignup() {
      return this.type === 'signup';
    },
  },
  methods: {
    ...mapActions('user', [
      'setUser',
    ]),
    validateForm(event) {
      this.errors = {
        name: '',
        email: '',
        password: '',
        form: '',
      };

      if (this.isSignup && !this.name) {
        this.errors.name = 'Required Field';
      }

      if (!this.email) {
        this.errors.email = 'Required Field';
      } else if (!this.validEmail(this.email)) {
        this.errors.email = 'Invalid Email Address';
      }

      if (!this.password) {
        this.errors.password = 'Required Field';
      }

      if (!this.errors.name && !this.errors.email && !this.errors.password) {
        this.submit();
      }

      event.preventDefault();
    },
    validEmail(email) {
      const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(email);
    },
    async submit() {
      const submitFn = this.isSignup ? signup : login;

      const res = await submitFn({
        name: this.name,
        email: this.email,
        password: this.password,
        latitude: this.latitude,
        longitude: this.longitude,
      });

      if (res.errors) {
        res.errors.forEach((error) => {
          this.errors[error.param || 'form'] = error.msg;
        });
      }

      if (res.data) {
        this.setUser(res.data);
      }
    },
    getGeoLocation() {
      navigator.geolocation.getCurrentPosition((position) => {
        const { latitude, longitude } = position.coords;

        this.longitude = parseFloat(longitude);
        this.latitude = parseFloat(latitude);
      }, (err) => {
        console.log(err);
      }, {
        timeout: 30000,
      });
    },
  },
  mounted() {
    this.getGeoLocation();
  },
};
</script>

