<template>
  <div :class="classes">
    <label :for="name">{{label}}</label>
    <input
      :name="name"
      :type="type"
      :value="value"
      @input="input"
    />
    <div class="textField__error">{{error}}</div>
  </div>
</template>

<script>
export default {
  name: 'VTextField',
  props: {
    name: {
      type: String,
      required: true,
    },
    label: {
      type: String,
      required: false,
    },
    value: {
      type: String,
      required: true,
    },
    type: {
      type: String,
      default: 'text',
    },
    error: String,
  },
  computed: {
    classes() {
      return {
        textField: true,
        'textField--error': this.error,
      };
    },
  },
  methods: {
    input(event) {
      this.$emit('input', event.target.value);
    },
  },
};
</script>

<style lang="scss" scoped>
$label-color: #acacac;
$input-color: #666;
$input-border-color: #b7b7b7;
$error-color: #ff5252;

.textField + .textField {
  margin-top: 20px;
}

.textField {
  margin-top: 0;
}

.textField label {
  color: $label-color;
  font-size: 0.8rem;
  font-weight: bold;
  display: block;
  text-align: left;
}

.textField input {
  width: 100%;
  height: 32px;
  font-size: 0.8rem;
  color: $input-color;
  border: 0;
  border-bottom: 1px solid $input-border-color;
}

.textField--error {
  label {
    font-weight: normal;
    color: $error-color;
  }

  input {
    border-bottom: 1px solid $error-color;
  }
}

.textField__error {
  text-align: left;
  font-size: 0.8rem;
  color: $error-color;
  margin-top: 5px;
  min-height: 1rem;
  transition: all 1s;
}
</style>
